<script>
	import { getContext } from 'svelte';

	const { data, xScale, yScale, custom } = getContext('LayerCake');

	export let hovered = null;
	export let selected = null;
	
	let coords = $custom.coords;
	let idKey = $custom.idKey;
	let labelKey = $custom.labelKey;
	// let colorHover = $custom.colorHover ? $custom.colorHover : 'orange';
	// let colorSelect = $custom.colorSelect ? $custom.colorSelect : '#206095';

</script>

{#if $coords}
<defs>
	<filter x="0" y="0" width="1" height="1" id="bgfillhov">
		<feFlood flood-color="rgba(255,255,255,0.8)" result="bg" />
		<feMerge>
			<feMergeNode in="bg"/>
			<feMergeNode in="SourceGraphic"/>
		</feMerge>
	</filter>
	<filter x="0" y="0" width="1" height="1" id="bgfill">
		<feFlood flood-color="rgba(255,255,255,0.8)" result="bg" />
		<feMerge>
			<feMergeNode in="bg"/>
			<feMergeNode in="SourceGraphic"/>
		</feMerge>
	</filter>
</defs>
<g class="label-group">
	{#if $coords[0] && $coords[0].x}
	{#each $coords as d, i}
		<!-- {#if [hovered, selected].includes($data[i][idKey])} -->
		<text
			class="label"
			transform="translate(5,-5)"
			filter="url(#bgfill)"
			fill="#333"
			x={$xScale(d.x)}
			y={$yScale(d.y)}>
			{$data[i][labelKey]}
		</text>
		<!-- {/if} -->
	{/each}
	{:else if $coords[0] && $coords[0][0] && $coords[0][0].x}
		{#each $coords as d, i}
			<!-- {#if [hovered, selected].includes($data[i][0][idKey])} -->
			<text
				class="label"
				transform="translate(2,3)"
				filter="url(#bgfill)"
				fill="#333"
				x={$xScale(d[d.length - 1].x)+10}
				y={$yScale(d[d.length - 1].y)}>
				{$data[i][0][labelKey]}
			</text>
		{/each}
	{/if}
</g>


<g class="hover-label-group">
	{#if $coords[0] && $coords[0][0] && $coords[0][0].x}
		{#each $coords as d, i}
			{#each d as dd}
				{#if hovered == dd}
					<text
						class="hover-label"
						filter="url(#bgfillhov)"
						fill="#333"
						x={$xScale(dd.x)-12}
						y={$yScale(dd.y)-15}>
							{dd.y}
					</text>
				{/if}
			{/each}
		{/each}
	{/if}
</g>


{/if}

<style>
	.hover-label {
		font-size: 0.8em;
		font-weight: 800;
	}
	.label {
		font-size: 0.8em;
	}
</style>